import { CrudService } from 'src/app/service/crud.service';
import { Chart } from 'chart.js';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { OwlOptions } from 'ngx-owl-carousel-o';
declare var $: any;

@Component({
  selector: 'app-project-details',
  templateUrl: './project-details.component.html',
  styleUrls: ['./project-details.component.scss']
})
export class ProjectDetailsComponent implements OnInit {
  modalRef: BsModalRef;
  title = 'My first AGM project';
  lat = 51.678418;
  lng = 7.809007;
  p: number = 1;
  data: any;
  routeState: any;
  images: any = [
    'https://via.placeholder.com/300.png/09f/fff',
    'https://via.placeholder.com/300.png/09f/fff',
    'https://via.placeholder.com/300.png/09f/fff'
  ]
  singleReport;
  indicator: any[];
  chart: any;
  activeChart: any = 'utilization';
  budgetChart: any;
  utilizationChart: any;
  output: any;
  customOptions: OwlOptions;

  constructor(private router: Router, private crud: CrudService, private modalService: BsModalService) {

    if (history.state.log) {
      this.data = JSON.parse(history.state.log);
      this.output = JSON.parse(history.state.output);
      //console.log(this.data);
    } else {
      this.router.navigate(['/home/projects'])
    }

    this.initiateCarousel();
  }

  ngOnInit() {
    this.initiateUtilizationChart();
    // $('[data-toggle="tooltip"]').tooltip();
    setInterval(() => {
      $('.logframes').DataTable();
    }, 1000)
  }

  calcPercentage(item) {
    let r = this.crud.calculatePercantage(item.actual, item.baseline, item.target, item.aggregation)
    if (r <= 33.3) {
      return 'red';
    }
    if (r > 33.3 && r <= 66.6) {
      return '#ffbf00';
    }
    if (r > 66.6) {
      return 'green';
    }
  }
  calc(r) {

    if (r <= 33.3) {
      return 'red';
    }
    if (r > 33.3 && r <= 66.6) {
      return '#ffbf00';
    }
    if (r > 66.6) {
      return 'green';
    }
  }

  initiateUtilizationChart() {

    if (this.activeChart != 'utilization') {
      this.activeChart = 'utilization';
      if (this.budgetChart) {

        this.budgetChart.clear();
        this.budgetChart.destroy();
      }
    }

    setTimeout(() => {
      this.utilizationChart = new Chart('myUtilizationChart', {
        type: 'line',
        data: {
          labels: ['QTR-1', 'QTR-2', 'QTR-3', 'QTR-4'], // your labels array
          datasets: [
            {
              // label: ["Stabilize the Macro Economic Environment", "Achieve Agriculture and Food Security", "Ensure Energy Sufficiency in Power and Petroleum Product", "Improve Transportation and Other Infrastructure", "Drive Industrilisation", "Improve health education and productivity of Nigerians", "Enhance social inclusion and reduce poverty", "Fight corruption and improve governance", "provide security for all citizens"],
              borderWidth: 2,
              borderColor: "#0277BD",

              data: [54, 34, 94, 62]
            },
          ]
        },
        options: {
          responsive: true,
          scaleFontColor: "#FFFFFF",
          legend: {
            display: false,
            labels: {
              fontColor: "#f7f7f2",
            }
          },
          tooltips: {
            mode: 'index',
            intersect: false,
          },
          hover: {
            mode: 'nearest',
            intersect: true
          },
          scales: {
            xAxes: [{
              display: true,
              barPercentage: 0.1,
              gridLines: {
                color: "#dadada4d"
              },
            }],
            yAxes: [{
              display: true,
              gridLines: {
                color: "#dadada4d"
              },
            }],
          }
        }
      });
    }, 200);
  }

  initiateBudgetChart() {

    if (this.activeChart != 'financial') {
      this.activeChart = 'financial';
      this.utilizationChart.clear();
      this.utilizationChart.destroy();
    }

    setTimeout(() => {
      this.budgetChart = new Chart('myFinancialChart', {
        type: 'bar',
        data: {
          labels: ['QTR-1', 'QTR-2', 'QTR-3', 'QTR-4'], // your labels array
          datasets: [
            {
              label: "Released",
              borderColor: "#FEA041",
              borderWidth: 2,
              backgroundColor: "#fea0411f",
              data: [62, 23, 73, 70, 46, 58, 38, 69, 98]
            },
            {
              label: "Utilized",
              borderColor: "#0277BD",
              borderWidth: 2,
              backgroundColor: "#0277bd1a",
              data: [23, 31, 16, 45, 92, 37, 76, 80, 76]
            },
          ]
        },
        options: {
          // onClick:this.initiateChartInterativity.bind(this),
          responsive: true,
          scaleFontColor: "#FFFFFF",
          legend: {
            display: false,
            labels: {
              fontColor: "#f7f7f2",
            }
          },
          scales: {
            xAxes: [{
              display: true,
              barPercentage: 0.5,
              categoryPercentage: 0.5,
              gridLines: {
                color: "#dadada4d"
              },
            }],
            yAxes: [{
              display: true,
              gridLines: {
                color: "#dadada4d"
              },
            }],
          }
        }
      });

    }, 200);
  }

  getNumberOfDays(start_date) {
    let currentDate = new Date().getTime();
    let start = new Date(start_date).getTime();
    return Math.floor((currentDate - start) / 86400000);
  }

  getLoc(data) {
    return (data + '%');
  }

  initiateCarousel() {
    this.customOptions = {
      loop: true,
      mouseDrag: true,
      touchDrag: true,
      pullDrag: true,
      dots: false,
      autoplay: true,
      navText: ['<img src="assets/icons/001-left-chevron.svg">', '<img src="assets/icons/002-right-chevron.svg">'],
      nav: true,
      responsive: {
        740: {
          items: 1
        },
        940: {
          items: 1
        },
        1200: {
          items: 1
        }
      }
    };
  }
}
