import { CrudService } from 'src/app/service/crud.service';
import { Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  types = ['Outcome', 'Output', 'Project']
  query: any;
  results: any;
  loaded: boolean;
  resultsCount: any;
  p: any;
  filters: any = [];
  filtered: any;
  constructor(private crud: CrudService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((param: any) => {
      this.query = param.params.query
      console.log(this.query);

      this.search();
    })
  }
  search() {

    const data = gql`
    query search($query: String!,) { 
      search(query:$query) 
      {
      projects{
        id
        title
        score
        images
        status
        logframes{
                  id
                  comments
                  challenges
                  outputValue
                  longitude
                  latitude
                  images
                  status
                  qow
                  loc
                  type
                  budget
                  approved
                  released
                  utilized
                  logframeStatus
                  project{
                    id
                    title
                  }

                 }
        budget
        mda{
          shortcode
        }
        output {
          id
        title
        actual
        baseline
        target
        
        mandates  {
          id 
          title
        }
        }
      }
        outputs {
          id
        title
        score
        actual
        baseline
        target
        mda{
          shortcode
        }
        }
        indicators {
          id
        title
        score
        actual
        baseline
        target
        mda{
          shortcode
        }
        }
        results
  }
}
`;
    this.crud.pullData(data, { query: this.query })
      .subscribe((result: any) => {
        let res = [...result.data['search'].projects, ...result.data['search'].outputs, ...result.data['search'].indicators];

        this.results = res.sort((a, b) => b.score - a.score);
        this.reset();
        this.filter(null, 'All');
        console.log(this.results);

        this.resultsCount = result.data['search'].results;
        this.loaded = true;
      });
  }

  prepareTarget(data, index) {
    if (data == null) return [];
    let tar = JSON.parse(data);
    // //console.log(tar);

    return tar[index];
  }
  // openProjectReports(item) {
  //   this.router.navigate(['/home/project/project-report-log'], {
  //     state: {
  //       log: JSON.stringify(item)
  //     }
  //   })

  // }
  reset() {
    this.filtered = this.results;
  }
  filter(ev, type) {

    if (ev.currentTarget.checked) {
      console.log(ev, type);
      this.filters.push(type);
    } else {
      this.filters.splice(this.filters.indexOf(type), 1)
      if (this.filters.length == 0) this.filtered = this.results
      return
    }
    this.filtered = this.results.filter(res => this.filters.includes(res.__typename));
  }
  openProjectReports(item, ind) {
    this.router.navigate(['/dashboard/project-details'], {
      state: {
        log: JSON.stringify(item),
        output: JSON.stringify(ind)

      }
    })

  }
}
