import { ToastrService } from 'ngx-toastr';
import gql from 'graphql-tag';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { CrudService } from '../service/crud.service';
import { ConstantPool } from '@angular/compiler';
// import { ActivityService } from '../service/activity.service';
declare const $: any;

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  componentName
  setactive = false;
  mdasidebar = false;
  lightmode = false;
  searchControl
  year = ['2019', '2020', '2021', '2022', '2023', '20124', '2025', '2026']

  menu = [
    {
      title: "Dashboard",
      url: "/dashboard/home",
      // icon: faTachometerAlt
    },
    {
      title: "Mandate",
      url: "/dashboard/mandates",
      // icon: faShoppingCart
    },
    {
      title: "Budget",
      url: "/dashboard/user-management",
      // icon: faReceipt
    },

  ];
  current: any;
  user: any;
  userAvailable: boolean;
  mdas: any;
  activeYear: string = '2020';
  search: any = { query: '' };
  constructor(private route: ActivatedRoute, private crud: CrudService, private toastr: ToastrService, private router: Router) {
    if (localStorage.getItem('mode')) {
      this.lightmode = JSON.parse(localStorage.getItem('mode'))
    }

    this.switchmode()
    this.current = (route.snapshot['_routerState'].url);
    this.user = JSON.parse(localStorage.getItem('user'));
    if (this.user != null) {
      this.userAvailable = true;

    }
    let activeYear = localStorage.getItem('activeYear')
    //console.log(activeYear);

    if (activeYear) this.activeYear = activeYear;
  }

  ngOnInit() {
    $(document).ready(function () {
      $('#sidebarCollapse').on('click', function () {
        $('#sidebar').toggleClass('active');
      });
    });
    // this.pullMdaByPerformance()

  }

  gotoProfile() {
    this.router.navigate(['/dashboard/user-profile'])
  }

  gotoSettings() {
    this.router.navigate(['/dashboard/system-settings'])
  }

  gotoDashboard() {
    this.router.navigate(['/']);
  }

  gotoEsw() {
    this.router.navigate(['/dashboard/early-warning'])
  }

  openMdaDashboard(id) {
    this.mdasidebar = false;
    this.router.navigate(['/dashboard/mda-dashboard/' + id])
    setTimeout(() => {
      location.reload();
    }, 500);
  }

  pullMdaByPerformance() {

    let data = gql`
        {
          mdasByPerformance {
          data
          }
        }
      `;
    this.crud.pullData(data, {})
      .subscribe(result => {
        this.mdas = JSON.parse(result.data["mdasByPerformance"].data);
        // this.assignCopy();
        // setInterval(() => {
        // 	// $('.usersTable').DataTable();
        // }, 2000);
      },
        error => {
          // this.toastr.error("Oops, we could not get you user's data, please check your network and try again", "Unsuccessful")
        });

  }

  switchmode() {
    if (this.lightmode) {
      localStorage.setItem('mode', 'true')
      document.documentElement.setAttribute('data-theme', 'light')
      return
    }

    document.documentElement.setAttribute('data-theme', 'dark')
    localStorage.setItem('mode', 'false')
  }

  logout() {
    this.user = null;
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    localStorage.removeItem('permissions');


    this.router.navigate(['/login'])
  }
  selectYear(year) {
    localStorage.setItem('activeYear', year);
    this.activeYear = year;
    this.toastr.success("Focus year changed to " + year + " successfully", "Success")

  }

  onSearch() {
    this.router.navigate(['/dashboard/search/' + this.search.query])
  }
}
