  scrollElementNext(): void {
    const el = document.querySelector<HTMLElement>('.horizontal-scroll');
    this.col = document.querySelector<HTMLElement>('.col-4').offsetWidth + this.col;
    if (this.col > el.offsetWidth) {
      this.col = 0;
    }
    el.scroll({
      top: 0,
      left: this.col,
      behavior: 'smooth'
    });
  }

  scrollElementPrev(): void {
    const el = document.querySelector<HTMLElement>('.horizontal-scroll');
    if (this.col <= 0) {
      this.col = el.offsetWidth;
    }
    this.col = this.col - document.querySelector<HTMLElement>('.col-4').offsetWidth;
    el.scroll({
      top: 0,
      left: this.col,
      behavior: 'smooth'
    });
  }
