import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DashboardHomeComponent } from './dashboard-home/dashboard-home.component';
import { AllOutcomesComponent } from './all-outcomes/all-outcomes.component';
import { AllMandatesComponent } from './all-mandates/all-mandates.component';
import { StrategicPlanComponent } from './strategic-plan/strategic-plan.component';
import { PresidentialMandateComponent } from './presidential-mandate/presidential-mandate.component';
import { MdaComponent } from './mda/mda.component';
import { UserLoginComponent } from './account/user-login/user-login.component';
import { AuthGuard } from './service/auth/auth.guard';
import { SettingsComponent } from './settings/settings.component';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { EwsComponent } from './ews/ews.component';
import { ProjectDetailsComponent } from './project-details/project-details.component';
import { OutputComponent } from './output/output.component';
import { PriorityComponent } from './priority/priority.component';
import { SearchComponent } from './search/search.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: UserLoginComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path: 'home',
        component: DashboardHomeComponent
      },
      {
        path: 'early-warning',
        component: EwsComponent
      },
      {
        path: 'system-settings',
        component: SettingsComponent
      },
      {
        path: 'user-profile',
        component: UserProfileComponent
      },
      {
        path: 'mda-dashboard/:data',
        component: StrategicPlanComponent
      },
      {
        path: 'mandates',
        component: PresidentialMandateComponent,
      },
      {
        path: 'mandates/result',
        component: MdaComponent
      },
      {
        path: 'project-details',
        component: ProjectDetailsComponent
      },
      {
        path: 'outcome',
        component: AllOutcomesComponent
      },
      {
        path: 'output',
        component: OutputComponent
      },
      {
        path: 'priority/:id',
        component: PriorityComponent
      },
      { path: 'search/:query', component: SearchComponent, data: { title: 'Search' } },
    ]
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy', useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
