import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { gql } from 'apollo-angular';
import { CrudService } from 'src/app/service/crud.service';

@Component({
  selector: 'app-user-login',
  templateUrl: './user-login.component.html',
  styleUrls: ['./user-login.component.scss']
})
export class UserLoginComponent implements OnInit {
  postForm: FormGroup;
  submitted: boolean;
  error: string;

  constructor(private formBuilder: FormBuilder, private router: Router, private crud: CrudService) {
    this.postForm = this.formBuilder.group({
      email: ["", [Validators.required]],
      password: ["", [Validators.required]],
    });

    let token = localStorage.getItem('token');
    if (token) {
      this.router.navigate(['/dashboard']);
    }
  }
  get f() {
    return this.postForm.controls;
  }

  ngOnInit() {
  }

  login() {
    this.submitted = true;
    if (this.postForm.invalid) {
      return;
    }

    // let query = gql`
    let query = gql`
    mutation login(
      $email: String!
      $password: String!){
        login(email: $email, password: $password) {
          token,
          user{
            id 
            name
            access
            email
            role{
              name
              permissions
            }
            mda{
              id
              name
              shortcode
            }
          }
        }
      }
    `;
    this.crud.postData(query, this.postForm.value).subscribe((result) => {
      if (!result.data['login']) {
        this.error = result.errors[0].message;
      }
      let access;
      localStorage.setItem('token', result.data['login'].token);

      localStorage.setItem('permissions', JSON.stringify(result.data['login'].user.role.permissions));
      localStorage.setItem('user', JSON.stringify(result.data['login'].user));
      localStorage.setItem('mda', JSON.stringify(result.data['login'].user.mda));
      localStorage.setItem('activeYear', '' + new Date().getFullYear())

      let activityInput = {
        type: 'Admin Access',
        operation: 'login',
        userId: result.data['login'].user.id,
      }
      this.router.navigate(['/dashboard'])
    })

  }
}
