import { Component, OnInit } from '@angular/core';
import { CrudService } from '../service/crud.service';
import gql from 'graphql-tag';
import { Router } from '@angular/router';
// import { Chart } from 'chart.js';
declare const Chart: any;
declare const $: any;

@Component({
  selector: 'app-dashboard-outcome-home',
  templateUrl: './dashboard-home.component.html',
  styleUrls: ['./dashboard-home.component.scss']
})
export class DashboardHomeOutcomeComponent implements OnInit {
  preffered: any = [];
  mdas: any;
  drive1: any;
  drive2: any;
  drive3: any;
  priorities: any;
  finPoliciesByMdas(arg0: any): any {
    throw new Error('Method not implemented.');
  }
  priority: any;
  chart: any;
  tab = 'revenue';
  pmore = false;
  mdamore = false;
  projectmore = false;
  policymore = false;
  fundmore = false;
  activeChart = 'priority'
  budgetCanvas: any;
  budgetChart: any;
  testBudgetChart: any;
  priorityChart: any;
  indicator: any;
  financials: any;
  finProjectsByMdas: any;
  budgetPriority = false;
  constructor(private crud: CrudService, private router: Router) {
    this.getPriorities();
  }

  ngOnInit() {
  }

  initializeGuage(data, id) {

    for (let i = 0; i < data.length; i++) {
      let currentGuage = document.getElementById(id + 'gauge' + i);
      const gauge = this.Gauge(currentGuage);
      const target = this.prepareTarget(data[i].target, 0).target;

      const percent = (this.crud.calculatePercantage(data[i].actual, data[i].baseline, target, data[i].aggregation)) / 100;
      gauge.value(percent);
    }
  }

  Gauge(el) {
    // ##### Private Properties and Attributes

    let element,      // Containing element for the info component
      data,         // `.gauge--data` element
      needle,       // `.gauge--needle` element
      value = 0.0,  // Current gauge value from 0 to 1
      prop;         // Style for transform

    // ##### Private Methods and Functions

    let setElement = function (el) {
      // Keep a reference to the various elements and sub-elements
      element = el;
      if (element != undefined) {

        data = element.querySelector(".gauge--data");
        needle = element.querySelector(".gauge--needle");
      }
    };

    var setValue = function (x) {
      value = x;
      var turns = -0.5 + (x * 0.5);
      if (data != undefined) {

        data.style[prop] = "rotate(" + turns + "turn)";
        needle.style[prop] = "rotate(" + turns + "turn)";
      }
    };

    // ##### Object to be Returned

    var exports = {
      element: function (el) {
        if (!arguments.length) { return element; }
        setElement(el);
        return this;
      },
      value: function (x) {
        if (!arguments.length) { return value; }
        setValue(x);
        return this;
      }
    };

    // ##### Public API Methods

    // ##### Initialization

    var body = document.getElementsByTagName("body")[0];
    ["webkitTransform", "mozTransform", "msTransform", "oTransform", "transform"].
      forEach(function (p) {
        if (typeof body.style[p] !== "undefined") { prop = p; }
      }
      );

    if (arguments.length) {
      setElement(el);
    }

    return exports;

  }

  prepareTarget(data, index) {
    if (data == null) return [];
    if (typeof data === 'number') {
      return { target: data };
    }
    const tar = JSON.parse(data);

    return tar[index];
  }

  viewPriorityDetails(p) {
    // localStorage.setItem('priority', JSON.stringify(p));
    this.router.navigate(['/dashboard/priority/' + p]);
  }

  setColor(data) {
    const target = this.prepareTarget(data.target, 0).target;
    const r = (this.crud.calculatePercantage(parseFloat(data.actual), parseFloat(data.baseline), parseFloat(target), data.aggregation));
    this.priorities.forEach(e => {
    });

    for (let i = 0; i < this.priorities.length; i++) {
      this.initializeGuage(this.priorities[i].lIndicators, 'priority' + i);
    }

    if (r <= 33.3) {
      return 'red';
    }
    if (r > 33.3 && r <= 66.6) {
      return '#ffbf00';
    }
    if (r > 66.6) {
      return 'green';
    }
  }

  getPriorities() {
    let data = gql`
    {
      priorities(tree: 2){
        id
        title
        order
        lIndicators{
            id
            title
            ref
            type
            baseline
            actual
            target
            aggregation
            rt
            currency
            active
            mdaId
            driver
            
          }
        }
      }
  `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.priorities = result.data.priorities;
        for (let i = 0; i < this.priorities.length; i++) {
          this.initializeGuage(this.priorities[i].lIndicators, 'priority' + i);
        }
      });
  }

  gotoOutcome(item) {
    this.router.navigate(['/dashboard/outcome'], {
      state: {
        log: JSON.stringify(item)
      }
    });
  }
}
